import React from 'react'

const TableHeader = () => {
    return(
        <thead>
            <tr>
                <td>Name</td>
                <td>Job</td>
                <td>Remove</td>
            </tr>
        </thead>
    )
}

const TableBody = props => {
    const rows = props.characterData.map((row, index) => {
        return (
            <tr key={index}>
                <td>
                    {row.name}
                </td>
                <td>
                    {row.job}
                </td>
                <td>
                    <button onClick={() => props.removeCharacter(index)}>Delete</button>
                </td>
            </tr>
        )
    })
    return(
       <tbody>{rows}</tbody>
    )
}

